html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "OpenSans", sans-serif;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.btn-reset {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
}

.container {
  min-width: 373px;
  padding: 0 16px;
  margin: 0 auto;
}

.cards__title {
  display: none;
}

.opacity-btn {
  display: none;
}

@font-face {
  font-family: "OpenSans";
  src: local(OpenSans-Regular), url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  src: local(OpenSans-Semibold), url("../fonts/OpenSans-Semibold.woff2") format("woff2"), url("../OpenSans-Semibold.woff") format("woff");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
.cards {
  padding: 20px 0;
}

.cards-item {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  border: 0.3px solid #828282;
  border-radius: 16px;
  overflow: hidden;
}
.cards-item__new {
  position: absolute;
  top: 41px;
  left: 0;
  min-width: 99px;
  padding: 8px 17px;
  text-align: center;
  font-weight: 600;
  font-size: 12px;
  line-height: 20px;
  color: #3E3E3E;
}
.cards-item__new--one {
  background-color: #099CE8;
}
.cards-item__new--two {
  background-color: #FED74B;
}
.cards-item__picture {
  width: 100%;
  height: 220px;
  vertical-align: bottom;
  object-fit: cover;
}
.cards-item__wrap {
  display: flex;
  flex-direction: column;
  padding: 12px 12px 19px;
}
.cards-item__time {
  display: inline-block;
  margin-bottom: 10px;
  padding-left: 25px;
  font-size: 12px;
  line-height: 20px;
  background-image: url("../../img/block-time.svg");
  background-size: 16px 16px;
  background-position: left center;
  background-repeat: no-repeat;
  color: #9E9E9E;
}
.cards-item__title {
  margin: 0;
  margin-bottom: 27px;
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  color: #404040;
}
.cards-item__list {
  margin-bottom: 37px;
}
.cards-item__item {
  display: flex;
  flex-direction: column;
  padding-left: 36px;
  font-size: 14px;
  line-height: 20px;
  background-image: url("../../img/ok.svg");
  background-size: 16px 12px;
  background-position: left 3px;
  background-repeat: no-repeat;
  color: #404040;
}
.cards-item__item:not(:last-child) {
  margin-bottom: 13px;
}
.cards-item__descr {
  display: block;
  margin-bottom: 7px;
}
.cards-item__box {
  display: flex;
  flex-wrap: wrap;
}
.cards-item__block-time {
  min-width: 63px;
  min-height: 24px;
  padding: 2px 13px;
  margin-bottom: 7px;
  border-radius: 11px;
  text-align: center;
  font-size: 14px;
  line-height: 20px;
  background-color: #D9E8FF;
  color: #343434;
  transition: background-color 0.3s ease-in-out;
  cursor: pointer;
}
.cards-item__block-time:not(:last-child) {
  margin-right: 7px;
}
.cards-item__block-time:hover {
  background-color: #9DBEF1;
}
.cards-item__btn {
  min-width: 63px;
  margin-bottom: 7px;
  border-radius: 11px;
  text-align: center;
  background-color: #D9E8FF;
}
.cards-item__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.cards-item__box-price {
  display: flex;
  flex-direction: column;
  margin-right: 5px;
}
.cards-item__price {
  margin-bottom: 4px;
  font-size: 30px;
  line-height: 20px;
  color: #000;
}
.cards-item__pier-price {
  font-size: 10px;
  line-height: 20px;
  color: #000;
}
.cards-item__link {
  display: block;
  min-width: 165px;
  padding: 11px 41px;
  border: 0.5px solid #CBA500;
  border-radius: 20px;
  font-size: 14px;
  line-height: 20px;
  background-color: #FED74B;
  color: #404040;
}

.cards-item:not(:last-child) {
  margin-bottom: 30px;
}

.active-time {
  background-color: #6BA6FF;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  color: #fff;
}

@media (min-width: 750px) {
  .container {
    max-width: 1186px;
    padding: 0 40px;
  }
  .cards-item {
    flex-direction: row;
  }
  .cards-item__picture {
    min-width: 309px;
    height: inherit;
  }
  .cards-item__wrap {
    padding: 16px 22px 26px 23px;
  }
  .cards-item__time {
    order: -1;
    font-size: 14px;
  }
  .cards-item__title {
    margin-bottom: 11px;
    order: -2;
    font-size: 26px;
    line-height: 34px;
    color: #000;
  }
  .cards-item__item {
    padding-left: 30px;
    font-size: 18px;
    color: #343434;
  }
  .cards-item__new {
    min-width: 154px;
    padding: 12px 35px;
    top: 46px;
    font-size: 16px;
  }
  .cards-item__descr {
    margin-right: 4px;
  }
  .cards-item__box {
    align-self: flex-start;
  }
  .cards-item__block-time {
    min-height: 28px;
    min-width: 73px;
    padding: 4px 14px;
    font-size: 16px;
  }
  .cards-item__inner {
    justify-content: flex-start;
    align-items: flex-end;
  }
  .cards-item__price {
    margin-bottom: 2px;
  }
  .cards-item__link {
    border: none;
  }
}
@media (min-width: 1024px) {
  .cards-item__picture {
    max-width: 473px;
  }
  .cards-item__item {
    flex-direction: row;
  }
}